$primary: #1576c8 !default;

@import 'bootstrap/bootstrap';
@import 'bootstrap/variables';

$font-primary: 'Muli',Arial,sans-serif;
$white: #fff;
$black: #000;


@mixin border-radius($radius) {
	-webkit-border-radius: $radius;
	-moz-border-radius: $radius;
	-ms-border-radius: $radius;
	border-radius: $radius;
}

@mixin transition($transition) {
	-moz-transition: all $transition ease;
	-o-transition: all $transition ease;
	-webkit-transition: all $transition ease;
	-ms-transition: all $transition ease;
	transition: all $transition ease;
}

html {
	// overflow-x: hidden;
}

body {
	font-family: $font-primary;
	background: $white;
	font-size: 16px;
	line-height: 1.8;
	font-weight: 300;
	color: lighten($black, 50%);
}

a {
	transition: .3s all ease;
	color: $primary;

	&:hover {
		text-decoration: none;
		color: $primary;
	}
}

h1,
h2,
h3,
h4,
h5,
.h1,
.h2,
.h3,
.h4,
.h5 {
	line-height: 1.5;
	color: $black;
	font-weight: 400;
}

.text-primary {
	color: $primary !important;
}

.navbar {
	padding: 0;
}

.ftco-navbar-light {
	//background: transparent!important;
	background: white;
	position: relative;
	left: 0;
	right: 0;
	z-index: 3;
	top: 0;
	border-bottom: 1px solid $primary;

	@include media-breakpoint-down(md) {
		//background: $black!important;
		color: $primary;
		top: 0;
		position: relative;
	}

	.navbar-nav {
		>.nav-item {
			>.nav-link {
				font-size: 20px;
				padding-top: 30px;
				padding-bottom: 30px;
				padding-left: 20px;
				padding-right: 20px;
				color: $primary;

				&:hover {
					color: $primary;
				}

				opacity: 1 !important;

				@include media-breakpoint-down(md) {
					padding-top: 10px;
					padding-bottom: 10px;
					padding-left: 30px;
					padding-right: 0px;
				}
			}


			&.ftco-seperator {
				position: relative;
				margin-left: 20px;
				padding-left: 20px;

				@include media-breakpoint-down(md) {
					padding-left: 0;
					margin-left: 0;
				}

				&:before {
					position: absolute;
					content: "";
					top: 10px;
					bottom: 10px;
					left: 0;
					width: 2px;
					background: rgba($white, .05);

					@include media-breakpoint-down(md) {
						display: none;
					}
				}
			}

			&.cta {
				>a {
					padding-left: 0;
					color: $white;

					span {
						border: 2px solid $white;
						display: inline-block;
						padding: 10px 20px;
						border-radius: 4px;
					}
				}

				&.cta-colored {
					span {
						border-color: $primary;
					}
				}
			}

			&:hover,
			&.active {
				>a {
					//color: rgba(255,255,255,.5);
					color: white !important;
					font-weight: bold;
					background: $primary;
				}
			}
		}
	}

	.navbar-toggler {
		border: none;
		color: $primary !important;
		cursor: pointer;
		padding-right: 0;
		text-transform: uppercase;
		font-size: 16px;
		letter-spacing: .1em;
	}

	&.scrolled {
		position: fixed;
		right: 0;
		left: 0;
		top: 0;
		margin-top: -130px;
		background: $white !important;
		box-shadow: 0 0 10px 0 rgba(0, 0, 0, .1);

		.nav-item {
			&.active {
				>a {
					color: white !important;
				}
			}
		}

		.navbar-nav {
			@include media-breakpoint-down(md) {
				background: none;
				border-radius: 0px;
				padding-left: 0rem !important;
				padding-right: 0rem !important;
			}
		}

		.navbar-nav {
			@include media-breakpoint-down(sm) {
				background: none;
				padding-left: 0 !important;
				padding-right: 0 !important;
			}
		}

		.navbar-toggler {
			border: none;
			color: rgba(0, 0, 0, .5) !important;
			border-color: rgba(0, 0, 0, .5) !important;
			cursor: pointer;
			padding-right: 0;
			text-transform: uppercase;
			font-size: 16px;
			letter-spacing: .1em;

		}

		.nav-link {
			padding-top: .9rem !important;
			padding-bottom: .9rem !important;
			color: $black !important;

			&.active {
				color: $primary !important;
			}
		}

		&.awake {
			margin-top: 0px;
			transition: .3s all ease-out;
		}

		&.sleep {
			transition: .3s all ease-out;
		}

		.navbar-brand {
			color: $black;

			img {
				height: 55px !important;
			}
		}

	}
}

.navbar-brand {
	font-weight: 400;
}

//OWL CAROUSEL
.owl-carousel {
	position: relative;
	overflow-x: hidden !important;

	.owl-item {
		overflow-x: hidden !important;
		opacity: .4;

		&.active {
			opacity: 1;
		}
	}

	.owl-nav {
		position: absolute;
		top: 50%;
		width: 100%;
		overflow-x: hidden !important;

		.owl-prev,
		.owl-next {
			position: absolute;
			transform: translateY(-50%);
			margin-top: -10px;
			@include transition(.7s);

			span {
				&:before {
					font-size: 40px;
				}
			}

			opacity: 0;
		}

		.owl-prev {
			left: 0;
		}

		.owl-next {
			right: 0;
		}
	}

	.owl-dots {
		text-align: center;

		.owl-dot {
			width: 10px;
			height: 10px;
			margin: 5px;
			border-radius: 50%;
			background: lighten($black, 90%);

			&.active {
				background: theme-color('primary');
			}
		}
	}

	&:hover {
		.owl-nav {

			.owl-prev,
			.owl-next {
				opacity: 1;
			}

			.owl-prev {
				left: -20px;
			}

			.owl-next {
				right: -20px;
			}
		}
	}

	// big slider
	&.home-slider {
		z-index: 0;
		position: relative;

		.slider-item {
			background-position: center !important;
			background-repeat: no-repeat;
			background-position: center center;
			height: calc(100vh - 285px);
			overflow: hidden;
			position: relative;
			background-size: cover;

			.overlay {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				content: '';
				background: $black;
				opacity: .2;
			}

			.slider-text {
				color: $white;
				height: calc(100vh - 117px);
				min-height: 700px;

				h1 {
					font-size: 72px;
					color: $white;
					line-height: 1.2;
					font-weight: 400;

					@include media-breakpoint-down(md) {
						font-size: 40px;
					}
				}

				p {
					font-size: 20px;
					line-height: 1.5;
					font-weight: 300;
					color: rgba(255, 255, 255, .8);

					strong {
						font-weight: 700;

						a {
							color: $white;
						}
					}
				}

				.breadcrumbs {
					text-transform: uppercase;
					font-size: 13px;
					letter-spacing: 1px;
					margin-bottom: 0;

					span {
						border-bottom: 2px solid rgba(255, 255, 255, .1);

						a {
							color: $white;
						}
					}
				}
			}
		}

		.owl-nav {

			.owl-prev,
			.owl-next {
				span {
					color: $white;
				}
			}
		}

		.owl-dots {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 40px;
			width: 100%;
			text-align: center;

			.owl-dot {
				width: 18px;
				height: 18px;
				margin: 5px;
				border-radius: 50%;
				background: lighten($black, 90%);
				background: none;
				border: 2px solid rgba(255, 255, 255, .5);
				outline: none !important;
				position: relative;
				transition: .3s all ease;
				display: inline-block;

				span {
					position: absolute;
					width: 6px;
					height: 6px;
					background: rgba(255, 255, 255, .5);
					border-radius: 50% !important;
					left: 50%;
					top: 50%;
					display: block;
					transform: translate(-50%, -50%);
				}

				&.active {
					border: 2px solid rgba(255, 255, 255, 1);

					span {
						background: rgba(255, 255, 255, 1);
					}
				}
			}
		}
	}
}

.owl-custom-nav {
	float: right;
	position: relative;
	z-index: 10;
	border: 1px solid red;

	.owl-custom-prev,
	.owl-custom-next {
		padding: 10px;
		font-size: 30px;
		background: #ccc;
		line-height: 0;
		width: 60px;
		text-align: center;
		display: inline-block;
	}
}


.bg-light {
	background: #fafafa !important;
}

.bg-primary {
	background: $primary;
}

.btn {
	cursor: pointer;
	border-radius: 0px;
	box-shadow: none !important;

	&:hover,
	&:active,
	&:focus {
		outline: none;
	}

	&.btn-primary {
		background: $primary;
		border: 2px solid $primary;
		color: $white;

		&:hover {
			border: 2px solid $primary;
			background: transparent;
			color: $primary;
		}

		&.btn-outline-primary {
			border: 2px solid $primary;
			background: transparent;
			color: $primary;

			&:hover {
				border: 2px solid transparent;
				background: $primary;
				color: $black;
			}
		}
	}

	&.btn-outline-white {
		border-color: rgba($white, .8);
		background: none;
		border-radius: 0px;
		border-width: 2px;

		&:hover,
		&:focus,
		&:active {
			background: $white;
			border-color: $white;
			color: $black;
		}
	}
}


//SEARCH
.ftco-section-search {
	background: white;
	border-bottom: 1px solid $primary;
	box-shadow: 1px 1px 8px $primary;
}

.tabulation-search {
	@include media-breakpoint-up(md) {
		margin-top: -60px;
	}

	.nav-pills {
		p {
			margin-bottom: 0;
		}

		.nav-link {
			float: left;
			border-radius: 0;
			margin-bottom: 0;
			background: $primary;
			color: white;
			min-width: 150px;
			text-align: center;

			&.active {
				background: white;
				color: $primary;

				@include media-breakpoint-up(md) {
					//margin-top: -10px;
				}
			}

			@include media-breakpoint-down(sm) {
				display: block;
				width: 100%;
			}
		}
	}
}


//ABOUT WITH VIDEO
.ftco-section-2 {
	.section-2-blocks-wrapper {
		.button {
			display: block;
			position: absolute;
			width: 100px;
			height: 100px;
			right: 0;
			margin-right: -50px;
			border-radius: 50%;
			background: $primary;
			z-index: 1;
			top: 50%;
			transform: translateY(-50%);
			animation: pulse 2s infinite;

			@include media-breakpoint-down(md) {
				top: auto;
				bottom: 0 !important;
				margin-right: auto;
				right: auto;
				left: 50%;
				margin-bottom: -50px;
				transform: translateX(-50%);
			}

			span {
				position: absolute;
				font-size: 30px;
				color: $white;
				top: 50%;
				left: 50%;
				transform: translate(-30%, -48%);
			}
		}

		.text {
			position: relative;
			z-index: -1;
			padding: 15px;

			@include media-breakpoint-up(md) {
				padding: 10% 7%;
				// margin-top: -3em;
				background: rgba(0, 0, 0, .02);
			}

			h3 {
				margin-bottom: 50px;
			}
		}

		.img {
			display: block;
			background-position: center center;
			background-size: cover;

			@include media-breakpoint-down(md) {
				height: 400px;
			}
		}
	}
}

@-webkit-keyframes pulse {
	0% {
		-webkit-box-shadow: 0 0 0 0 rgba($primary, 0.4);
	}

	70% {
		-webkit-box-shadow: 0 0 0 30px rgba($primary, 0);
	}

	100% {
		-webkit-box-shadow: 0 0 0 0 rgba($primary, 0);
	}
}

@keyframes pulse {
	0% {
		-moz-box-shadow: 0 0 0 0 rgba($primary, 0.4);
		box-shadow: 0 0 0 0 rgba($primary, 0.4);
	}

	70% {
		-moz-box-shadow: 0 0 0 30px rgba($primary, 0);
		box-shadow: 0 0 0 30px rgba($primary, 0);
	}

	100% {
		-moz-box-shadow: 0 0 0 0 rgba($primary, 0);
		box-shadow: 0 0 0 0 rgba($primary, 0);
	}
}


//PROMO
.promo {
	@include media-breakpoint-down(sm) {
		margin-bottom: 30px;
	}

	.promo-img {
		display: block;
		height: 200px;
		width: 200px;
		margin: 0 auto;
		position: relative;
		@include border-radius(50%);
		-webkit-box-shadow: 0px 10px 59px -27px rgba(0, 0, 0, 1);
		-moz-box-shadow: 0px 10px 59px -27px rgba(0, 0, 0, 1);
		box-shadow: 0px 10px 59px -27px rgba(0, 0, 0, 1);
	}

	.text {
		h2 {
			font-size: 20px;
			font-weight: 600;
		}

		.price {
			font-size: 20px;

			span {
				font-size: 16px;
				color: lighten($black, 80%);
			}
		}

		.read {
			text-transform: uppercase;
			font-size: 11px;
			font-weight: 400;
			letter-spacing: 1px;
		}
	}
}


//SERVICES
.services {
	position: relative;

	&:before,
	&:after {
		position: absolute;
		content: "";
		background: lighten($primary, 45%);
	}

	.icon {
		width: 120px;
		height: 120px;
		background: lighten($black, 95%);
		@include border-radius(50%);

		span {
			color: $primary;
		}
	}

	.media-body {
		.heading {
			font-size: 20px;
		}
	}
}

//POPULAR DESTINATION
//### .block-5
.block-5 {
	overflow: hidden;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	height: 350px;
	position: relative;
	display: block;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: -moz-linear-gradient(top, transparent 0%, transparent 18%, rgba(0, 0, 0, 0.8) 99%, rgba(0, 0, 0, 0.8) 100%);
		background: -webkit-linear-gradient(top, transparent 0%, transparent 18%, rgba(0, 0, 0, 0.8) 99%, rgba(0, 0, 0, 0.8) 100%);
		background: linear-gradient(to bottom, transparent 0%, transparent 18%, rgba(0, 0, 0, 0.8) 99%, rgba(0, 0, 0, 0.8) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#cc000000', GradientType=0);
		opacity: .8;
	}

	.text {
		position: absolute;
		bottom: 0;
		right: 0;
		left: 0;
		padding: 20px 20px 10px 20px;
		transition: .8s all ease;

		&:before {
			bottom: 0;
			left: 0;
			right: 0;
			z-index: 1;
			background: $white;
			visibility: hidden;
			opacity: 0;
			height: 0;
			position: absolute;
			content: "";
			@include transition(.3s);
		}

		.heading,
		.subheading,
		.post-meta,
		.excerpt,
		.price,
		.star-rate {
			z-index: 2;
			position: relative;
		}

		.subheading {
			color: $white;
			text-transform: uppercase;
			letter-spacing: .1em;
			font-size: 12px;
			margin-bottom: 5px;
			opacity: .6;
		}

		.heading {
			color: $white;
			margin: 0 0 10px 0;
			padding: 0;
			font-weight: bold;
			font-size: 24px;
			line-height: 1.2;
			font-weight: 400;
		}

		.post-meta {
			line-height: 1.4;
			color: $white;
			font-size: 14px;

			// text-transform: uppercase;
			span {
				display: inline-block;
				margin-right: 10px;
				margin-bottom: 10px;
				opacity: .6;

			}
		}

		.price {
			color: $white;
			font-weight: bold;
		}

		.star-rate {
			font-size: 12px;
			color: rgba(2552, 255, 255, 1);

			span {
				margin-right: 2px;
			}
		}

		.excerpt {
			line-height: 1.4;
			color: $white;
		}
	}

	&:hover,
	&:focus {
		.text {

			&:before {
				visibility: visible;
				opacity: 1;
				height: 100%;
				background: $primary;
				color: white !important;
			}

			.heading,
			.subheading,
			.post-meta,
			.price,
			.star-rate {
				//color: $black;
			}

		}
	}
}

