		&:before {
			position: absolute;
			top: 0;
			left: 0;
			content: '';
			width: 100px;
			height: 100vh;
			-webkit-box-shadow: inset 105px 0px 70px -60px rgba(255, 255, 255, 1);
			-moz-box-shadow: inset 105px 0px 70px -60px rgba(255, 255, 255, 1);
			box-shadow: inset 105px 0px 70px -60px rgba(255, 255, 255, 1);
			z-index: 1;
		}

		&:after {
			position: absolute;
			top: 0;
			right: 0;
			content: '';
			width: 100px;
			height: 100vh;
			-webkit-box-shadow: inset -105px 0px 70px -60px rgba(255, 255, 255, 1);
			-moz-box-shadow: inset -105px 0px 70px -60px rgba(255, 255, 255, 1);
			box-shadow: inset -105px 0px 70px -60px rgba(255, 255, 255, 1);
